.button {
  color: #1d1d1d;
  border-radius: 40px;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  height: 56px;
  text-align: center;
  background-color: white;
  border-color: transparent;
}

.button:hover {
  box-shadow: 1px 4px 16px 0 #e8e8f066;
}

.button:active {
  box-shadow: 1px 4px 16px 0 #e8e8f0a3;
}
